.storeNavDiv{
    display: flex;
}

/* General Nav Styles */
.storeNav {
    color: var(--white);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--black);
    width: 100%;
    position: fixed;
    z-index: 2;
}

.storeNavTop{
    display: flex;
    justify-content: space-between;
    direction: ltr;
    align-items: center;
}

.storeNavHeading{
    margin: 0;
    font-size: 30px;
    text-transform: uppercase;
}

.storeNavEmptyDiv{
    width: 96px;
    padding: 0 10px;
}


/* bottom */
.storeNavBottom{
    width: 100%;
    background-color: var(--black);
    display: flex;
    justify-content: center;
    border-bottom: .5px solid var(--white);
}

/* Navigation List */
.storeNavList {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    height: 100%;
    width: 80%;
    padding: 0;
    margin: 0;
}


/* Navigation Items */
.storeNavItem {
    position: relative;
    font-size: 16px;
    font-family: "Titillium Web", sans-serif;
    font-weight: lighter;
    margin-bottom: 10px;
    font-weight: 300;
}

.storeNavItem::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 2px;
    background-color: var(--white);
    transition: width 0.3s ease, left 0.3s ease; 
}

.storeNavItem:hover::after {
    width: 100%;
}

/* Hebrew Language Nav */
.storeNavHebrew {
    display: flex;
    direction: rtl;
}


/* mobile nav */
.mobileNav{
    display: none;
}

/* -------------------------- media query -------------------------- */
@media (max-width: 630px) {
    .storeNav {
        display: none;
    }
    
    .mobileNav {
        display: block;
        position: fixed; 
        z-index: 2;
        width: 100%;
        color: var(--white);
        background-color: var(--black);
        padding: 15px 0;
    }

    .mobileNavButton {
        padding: 0 10px;
        z-index: 1000;
        display: flex;
        justify-content: end;
    }

    .mobileNavIcon:hover {
        cursor: pointer;
        transform: scale(1.1);
        transition: transform 0.5s;
    }

    .slidingMenuNav {
        position: fixed;
        top: 0;
        right: 0; /* Ensure the menu slides in/out from the right */
        height: 100vh;
        width: 100%;
        background-color: var(--black);
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translateX(100%); /* Start off-screen */
        transition: transform 0.5s ease; /* Smooth slide effect */
    }

    .slideMenuLeft {
        transform: translateX(0); /* Slide into view */
    }

    .storeNavList {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .storeNavItem {
        font-size: 40px;
    }
}

@media (max-width: 350px) {
    .storeNavHeading{
        font-size: 25px;
    }
}

@media (max-width: 300px) {
    .storeNavHeading{
        font-size: 20px;
    }
}