/* Main content styling */
.infoPageMainContent {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
}

/* Carousel container styling */
.carouselContainer {
    margin-top: 250px;
    /* height: auto; */
    display: flex;
    justify-content: center;
    align-items: end;
    width: 90%;
    position: relative;
}

/* Carousel wrapper styling for transitions */
.carouselWrapper {
    transition: transform 0.3s, filter 0.3s, opacity 0.3s;
}

/* Company text styling */
.companyText {
    width: 60%;
    text-align: justify;
    font-size: 110%;
    padding: 20px;
}

/* Text and arrows container styling */
.textAndArrows {
    margin: 0 40px 0 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    direction: ltr;
    margin-bottom: 100px;
}

/* Arrow icon hover effect */
.arrowIcon:hover {
    cursor: pointer;
    transform: scale(110%);
    transition: 1s;
}


/* articlesSection */
.articlesSection{
    display: flex;
    width: 80%;
    justify-content: space-between;
    margin-bottom: 40px;
}




/* responsiveness */
@media (max-width: 1300px){
    .articlesSection{
        flex-direction: column;
        align-items: center;
        width: auto;
        gap: 40px;
    }
}

@media (max-width: 1190px){
    /* carousel container */
    .carouselContainer {
        margin-top: 250px;
    }
    
    /* carousel main text */
    .companyText{
        width: 80%;
        margin: 0;
    }
}

@media (max-width: 910px){
    /* carousel container */
    .carouselContainer {
        margin-top: 250px;
    }

    .textAndArrows{
        width: 100%;
    }

    /* carousel main text */
    .companyText{
        font-size: 15px;
    }
}

@media (max-width: 780px){
    /* carousel container */
    .carouselContainer {
        margin-top: 150px;
    }
}

@media (max-width: 600px){
    /* carousel container */
    .carouselContainer {
        margin-top: 100px;
    }

    .articlesSection{
        flex-direction: column;
        align-items: center;
        gap: 40px;
    }
}

@media (max-width: 600px){
    .companyText{
        padding: 20px 0;
        font-size: 13px;
    }
}