.artStorePage{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.mainContent{
    display: flex;
    margin-top: 100px;
}

.cards{
    padding: 50px 10px;
    display: flex;
    justify-content: center;
    
    flex-wrap: wrap;
    gap: 100px 30px;
    flex: 1;
    border-right: 1px solid var(--black);
}


/* responsiveness */
@media (max-width: 830px){
    .storeMenu{
        min-width: 131px;
    }
}

@media (max-width: 630px){
    .mainContent{
        margin-top: 60px;
    }
}
