.articleImageAndCopyright{
    height: 350px;
    width: 500px;
    position: relative;
}

.articleImage{
    height: 350px;
    width: 500px;
    object-fit: cover;
    object-position: center;
    border: 1px solid var(--black);
}

.articleImage:hover{
    cursor: pointer;
}

.credit{
    position: absolute;
    font-size: x-small;
    transform: translate(1px, 12px);
    width: 500px;
    background-color: rgba(0, 0, 0, 0.642);
    color: var(--white);
    bottom: 0;
    
}

/* title */
.article h3{
    cursor: pointer;
    font-size: 18px;
}


/* responsiveness */
@media (max-width: 1300px){
    /* heading */
    .article h3{
        text-align: center;
    }
}

@media (max-width: 600px){
    .articleImageAndCopyright{
        height: 350px;
        width: 500px;
        position: relative;
    }

    .articleImage{
        height: 350px;
        width: 500px;
        object-fit: cover;
        object-position: center;
    }

    .credit{
        width: 500px;
    }

    .article h3{
        width: 500px;
        text-align: center;
    }
}

@media (max-width: 530px){
    .articleImageAndCopyright{
        height: 300px;
        width: 400px;
        position: relative;
    }

    .articleImage{
        height: 300px;
        width: 400px;
        object-fit: cover;
        object-position: center;
    }

    .credit{
        width: 400px;
    }

    .article h3{
        width: 400px;
        text-align: center;
    }
}

@media (max-width: 420px){
.articleImageAndCopyright{
        height: 150px;
        width: 250px;
        position: relative;
    }

    .articleImage{
        height: 150px;
        width: 250px;
        object-fit: cover;
        object-position: center;
    }

    .credit{
        width: 250px;
    }

    .article h3{
        width: 250px;
        text-align: center;
        font-size: 13px;
    }
}