.hero{
    /* margin-top: 160px; */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    direction: ltr;
}

.heroContent{
    margin-top: 100px;
    height: 70%;
    width: 90%;
    display: flex;
    box-shadow: 0 0 10px rgba(9, 9, 9, 0.8); /* horizontal-offset vertical-offset blur-radius color */
}

.heroVideoDiv{
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.heroVideo{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}


/* responsivness */
@media (max-width: 1230px) {
    .heroContent{
        display: flex;
        justify-content: center;
        flex-direction: column-reverse;
    }
}

@media (max-width: 630px){
    .heroContent{
        margin: 0;
    }
}