.languageButtonAndIcon{
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0 10px;
    opacity: 1;
    width: 100px;
}

.languageIcon{
    margin-right: 10px;
}

.languageIcon:hover{
    transform: scale(110%);
    transition: .5s;
}

@media (max-width: 630px){
    .languageButtonAndIcon p{
        display: none;
    }

    .languageButtonAndIcon{
        width: auto;
        margin-left: 10px;
        padding: 0;
    }
}