.articlePage{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.articlePageDiv{
    margin-top: 150px;
    width: 700px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/* image */
.articlePageImage{
    width: 700px;
    position: relative;
}

/* copy rights */
.articlePageCredit{
    width: 700px;
    direction: ltr;
}

/* heading */
.articlePageHeading{
    font-size: 21px;
}

/* text */
.articlePageText{
    text-align: justify;
    text-wrap: wrap;
    font-family: var(--body-font);
    margin-bottom: 50px;
}


/* responsiveness */
@media (max-width: 730px){
    /* article page */
    .articlePage{
        align-items: center;
    }

    /* main div */
    .articlePageDiv{
        width: 500px;
        align-items: center;
    }

    /* article image */
    .articlePageImage{
        width: 500px;
    }

    /* copy rights */
    .articlePageCredit{
        width: 500px;
    }

    /* heading */
    .articlePageHeading{
        font-size: 19px;
    }

    /* text */
    .articlePageTextDiv{
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .articlePageText{
        text-align: justify;
        text-wrap: wrap;
        font-family: var(--body-font);
        width: 500px;
    }
}

@media (max-width: 630px){
    /* main div */
    .articlePageDiv{
        margin-top: 100px;
    }
}

@media (max-width: 530px){
    /* main div */
    .articlePageDiv{
        width: 350px;
    }

    /* copy rights */
    .articleImageAndCopyright{
        width: 350px;
    }

    /* article image */
    .articlePageImage{
        width: 350px;
    }

    /* copy rights */
    .articlePageCredit{
        width: 350px;
    }
    
    /* heading */
    .articlePageHeading{
        width: 350px;
        font-size: 17px;
    }

    /* text */
    .articlePageText{
        font-family: var(--body-font);
        width: 350px;
    }
}