.fashionStore{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.fashionStoreCardContainer{
    padding: 50px 10px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 100px 30px;
    flex: 1;
}

.pagination{
    display: flex;
    align-items: center;
    margin: 0;
    padding: 20px;
    border-top: 1px solid var(--black);
}

.noItemsMessage{
    margin-top: 100px;
}