/* General styles for the featured items container */
.featuredItems {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 80px;
    border: 1px solid black;
    margin-top: 80px;
    margin-left: 5px;
    margin-right: 5px;
    overflow: hidden;
}

/* Styles for the fashion row section */
.featuredItemsFashionRow {
    display: flex;
    gap: 30px;
    justify-content: end;
}

.featuredItemsFashionRowH2 {
    margin: 0;
    font-size: 30px;
}

.featuredItemsFashionRowTextAndButton, .featuredItemsArtRowTextAndButton {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 30%;
    height: 400px;
}

.featuredItemsFashionRowP, .featuredItemsArtRowP {
    font-size: 20px;
    margin: 0;
    margin-top: 30px;
}

.featuredItemsFashionRowTextAndButton button {
    all: unset;
    background-color: black;
    color: white;
    padding: 10px;
    width: 150px;
    text-align: center;
    font-family: var(--body-font);
    text-transform: uppercase;
}

.featuredItemsFashionRowCards {
    display: flex;
    gap: 40px;
    justify-content: end;
}

/* Styles for the art row section */
.featuredItemsArtRow {
    display: flex;
    gap: 30px;
    justify-content: start;
    flex-direction: row-reverse;
}

.featuredItemsArtRowH2 {
    margin: 0;
    font-size: 30px;
}

.featuredItemsArtRowTextAndButton button {
    all: unset;
    background-color: black;
    color: white;
    padding: 10px;
    width: 150px;
    text-align: center;
    font-family: var(--body-font);
    text-transform: uppercase;
}

.featuredItemsArtRowCards {
    display: flex;
    gap: 40px;
    justify-content: end;
}


/* responsiveness */
@media (max-width: 1335px){
    /* fashion row */
    .featuredItemsFashionRow{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .featuredItemsFashionRowTextAndButton{
        width: 70%;
        display: flex;
        justify-content: center;
        text-align: center;
        gap: 30px;
        height: auto;
    }

    /* art row */
    .featuredItemsArtRow{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .featuredItemsArtRowTextAndButton{
        width: 70%;
        display: flex;
        justify-content: center;
        text-align: center;
        gap: 30px;
        height: auto;
    }

    .featuredItemsBtnDiv{
        order: 1;
    }
}

@media (max-width: 1020px){
    /* fashion */
    .featuredItemsFashionRowCards, .featuredItemsArtRowCards{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
}