/* Container for the entire item page */
.itemPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: var(--body-font);
    direction: ltr;
}

/* Main content container */
.itemPageMainContent {
    margin-top: 136px;
    display: flex;
    height: 80vh;
    width: 80%;
    border: 1px solid var(--black);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Adjusted for better shadow */
    background-color: var(--white);
}

/* Left side container */
.itemPageMainContentLeft {
    width: 50%;
    border-right: 1px solid var(--black);
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
}

/* Main Image Section */
.mainImage {
    position: relative;
    flex: 9; /* Takes up 80% of the available height */
    height: 500px; /* Fixed height */
    overflow: hidden;
    display: flex;
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center content vertically */
}

/* Main image styling */
.currentMainImage img {
    max-width: 100%; /* Ensure the image fits within the container */
    max-height: 100%; /* Ensure the image fits within the container */
    object-fit: contain; /* Maintain aspect ratio without stretching */
    object-position: center; /* Center the image within the container */
}

/* Carousel buttons container */
.carouselButtonsDiv {
    position: absolute;
    top: 50%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    transform: translateY(-50%); /* Center vertically */
}

/* Individual carousel button styling */
.carouselButton {
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: var(--white);
    background-color: var(--black);
    opacity: 0; /* Hidden by default */
    transition: opacity 0.3s ease, transform 0.3s ease;
}

/* Show carousel buttons on hover */
.mainImage:hover .carouselButton {
    opacity: 0.7;
}

/* Carousel button hover effect */
.carouselButton:hover {
    transform: scale(1.2);
}

/* Thumbnail images container */
.otherImages {
    display: flex;
    flex: 2; /* Takes up the remaining 20% of the height */
    justify-content: start;
    align-items: start;
    padding: 10px 0 0 10px;
    overflow-x: scroll; /* Ensure horizontal scrolling */
    scrollbar-width: auto; /* Always show scrollbar for modern browsers */
    scrollbar-color: var(--black) var(--light-gray); /* Customize scrollbar colors */
    margin-right: 10px;
}

/* Always-visible scrollbar for Webkit-based browsers (Chrome, Safari, Edge) */
.otherImages::-webkit-scrollbar {
    height: 8px; /* Adjust scrollbar height */
    visibility: visible; /* Ensure scrollbar is visible */
    background-color: var(--light-gray); /* Track color */
    width: 50%; /* Adjust scrollbar width to 50% */
}

.otherImages::-webkit-scrollbar-thumb {
    background-color: var(--black); /* Scrollbar thumb color */
    border-radius: 4px; /* Rounded edges for scrollbar thumb */
}

/* Optional: Add hover effect for the scrollbar thumb */
.otherImages::-webkit-scrollbar-thumb:hover {
    background-color: var(--primary-color); /* Change color on hover */
}

.otherImagesDiv {
    display: flex;
    width: 100%;
    gap: 10px;
    justify-content: space-between;
}


/* Thumbnail image styling */
.thumbnailImage {
    height: 90px;
    width: 90px;
    border: 0.5px solid var(--black);
    object-fit: cover;
    transition: transform 0.3s ease;
    cursor: pointer;
}

/* Thumbnail image hover effect */
.thumbnailImage:hover {
    transform: scale(1.1);
}

/* -------------------- Right side container -------------------- */
.itemPageMainContentRight {
    width: 50%;
    padding: 5px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

/* Item heading styling */
.itemHeading {
    font-size: 40px;
    text-transform: uppercase;
    font-family: var(--body-font);
    margin: 0;
}

/* Item description styling */
.itemDescription {
    font-size: 20px;
    margin: 30px 0;
}

/* Item price styling */
.itemPrice {
    font-weight: 500;
}

/* Check availability button styling */
.checkIfAvailableButton {
    all: unset;
    background-color: var(--black);
    color: var(--white);
    padding: 10px 5px;
    width: 30%;
    text-align: center;
    cursor: pointer;
    transition: transform 0.3s ease;
}

/* Check availability button hover effect */
.checkIfAvailableButton:hover {
    transform: scale(1.05);
}

/* return and care div */
.returnAndCareDiv{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
}

/* return policy */
.returnPolicyDiv{
    /* height: 100px; */
}

.returnPolicyHeadingAndArrow{
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 13px;
}

.returnPolicyHeadingAndArrow:hover{
    cursor: pointer;
}

.returnPolicyHeading{
    margin: 0;
    text-transform: uppercase;
}

.returnPolicyText{
    margin-bottom: 0;
    font-size: 13px;
}

/* Share item section */
.itemShare {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
    font-size: 20px;
    width: 80%;
}

/* Icon styling for share buttons */
.icon {
    cursor: pointer;
    color: var(--black);
    transition: color 0.3s ease;
}

/* Icon hover effect */
.icon:hover {
    color: var(--primary-color);
}


/* responsiveness */
@media (max-width: 990px){
    .itemPageMainContent{
        flex-direction: column;
        height: 100%;
        border: none;
        box-shadow: none;
    }

    /* left side */
    .itemPageMainContentLeft{
        width: 100%;
        border: none;
        padding-bottom: 10px;
    }
    
    .currentMainImage{
        width: 50%;
    }

    .otherImagesDiv{
        justify-content: space-between;
        width: 100%;
        padding-bottom: 10px;
    }
    
    .carouselButtonsDiv{
        width: 50%;
    }

    .carouselButton{
        opacity: .7;
    }


    /* right side */
    .itemPageMainContentRight{
        width: 100%;
        flex-direction: row;
        justify-content: center;
        border: 1px solid var(--black);
        padding: 30px 0;
    }
    
    .itemDetails{
        display: flex;
        flex-direction: column;
        width: 90%;
        gap: 100px;
    }

    .itemPageEmptyDiv{
        display: none;
    }
}

@media (max-width: 630px){
    .itemPageMainContent{
        margin-top: 100px;
    }

    .currentMainImage{
        width: 70%;
    }
    
    .carouselButtonsDiv{
        width: 70%;
    }
}