.socialMediaBlock{
    background-color: white;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.socialMediaIconsList{
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 100px;
}

.icon:hover{
    transform: scale(110%);
    transition: 0.5s;
}

/* --------------- media query --------------- */
@media (max-width: 1230px) {
    .socialMediaBlock {
        display: flex;
        width: 100%;
        height: 100px;
    }

    .socialMediaIconsList{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin: 0;
    }

    .icon{
        margin: 0;
    }
}

@media (max-width: 530px) {
    .socialMediaIconsList{
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
    }
}

@media (max-width: 280px) {
    .socialMediaBlock{
        height: auto;
        padding: 10px 0;
    }
}